import React, { useState,useEffect } from "react";
import { useHistory } from "react-router-dom";
import M from "materialize-css";

const Createpost = () => {
  const history = useHistory();
  const [title, setTitle] = useState(" ");
  const [body, setBody] = useState(" ");
  const [image, setImage] = useState(" ");
  const [url, setUrl] = useState("");
  
    useEffect(()=>{

    
     if(url){
      fetch("/createpost", {
        method: "post",
        headers: {
          "Content-Type": "application/json",
          "Authorization": "Bearer " +localStorage.getItem("jwt"),
        },
        body: JSON.stringify({
          title,
          body,
          photo:url
        }),
      })
        .then((res) => res.json())
        .then(data => {
          console.log(data);
          if (data.error){ M.toast({ html: data.error, classes: "#f44336 red" });
        }else {
            M.toast({
              html: "created post successully",
              classes: "#4caf50 green" });
            
            history.push("/profile");
          }
        })
        .catch((err) => {
          console.log(err);
        }); 
  }
    },[url])
   
  
  const postDetails = () => {
    const data = new FormData();
    data.append("file", image);
    data.append("upload_preset", "insta-clone");
    data.append("cloud_name", "dvjjzsilz");
    fetch("https://api.cloudinary.com/v1_1/dvjjzsilz/image/upload", {
      method: "post",
      body: data,
    })
      .then(res => res.json())
      .then(data => {
        console.log(data)
        setUrl(data.url);
      })
      .catch((err) => { 
        console.log(err);
      });
     
  };

  return (
    <div
      className="card input field"
      style={{
        margin: "30px auto",
        maxWidth: "500px",
        padding: "20px",
        textAlign: "center",
      }}
    >
      <input
        type="text"
        placeholder="title"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
      />
      <input
        type="text"
        placeholder="body"
        value={body}
        onChange={(e) => setBody(e.target.value)}
      />
      <div class="file-field input-field">
        <div className="btn #2196f3 blue darken">
          <span>Upload image</span>
          <input type="file" onChange={(e) => setImage(e.target.files[0  ])} />
        </div>
        <div class="file-path-wrapper">
          <input class="file-path validate" type="text" />
        </div>
      </div>
      <button className="btn #2196f3 blue" onClick={() => postDetails()}>
        Submit Post
      </button>
    </div>
  );
};

export default Createpost;